.portfolioContainer {
	background-color: #08101f;
	overflow: auto;
	padding-bottom: 80px;
}

.portfolioInnerContainer {
	max-width: 1140px;
	margin: auto;
	padding: 5%;
}

.portfolio {
	width: 419px;
	height: 669px;
	margin-right: 55px;
	margin-bottom: 60px;

	float: left;
	padding: 45px;
	border-radius: 10px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
	-webkit-transition: scale 0.25s;
	-o-transition: scale 0.25s;
	transition: scale 0.25s;
}
.portfolio:hover {
	scale: 0.95;
}

.title {
	color: white;
	text-align: left;
	font-size: 3em;
	font-weight: 800;
	-webkit-transition: font-size 0.5s, text-align 0.5s;
	-o-transition: font-size 0.5s, text-align 0.5s;
	transition: font-size 0.5s, text-align 0.5s;
}

.projectName {
	text-align: left;
	color: #fffffff0;
	font-size: 1.2em;
	font-weight: 750;
}

.projectDes {
	text-align: left;
	color: #fffffff0;
	font-size: 2em;
	font-weight: 750;
}

.projectImg {
	width: 100%;
}

@media only screen and (max-width: 1265px) {
	.portfolio {
		width: 390px;
		height: 620px;
		margin-right: 50px;
		float: left;
		padding: 45px;
	}
	.projectImg {
		width: 100%;
	}
}
@media only screen and (max-width: 1182px) {
	.portfolio {
		width: 360px;
		height: 572px;
		margin-right: 80px;
		float: left;
		padding: 35px;
	}

	.projectImg {
		width: 80%;
	}
}

@media only screen and (max-width: 1178px) {
	.portfolio {
		width: 340px;
		height: 510px;
		margin-right: 80px;
		float: left;
		padding: 30px;
	}

	.projectImg {
		width: 80%;
	}

	.projectName {
		font-size: 1.1em;
	}

	.projectDes {
		font-size: 1.5em;
	}
}

@media only screen and (max-width: 1069px) {
	.portfolio {
		width: 300px;
		height: 450px;
		margin-right: 70px;
		float: left;
		padding: 30px;
	}

	.projectImg {
		width: 80%;
	}

	.projectName {
		font-size: 0.9em;
	}

	.projectDes {
		font-size: 1.3em;
	}
	.title {
		font-size: 2em;
	}
}

@media only screen and (max-width: 959px) {
	.portfolio {
		width: 280px;
		height: 420px;
		margin-right: 25px;
		float: left;
		padding: 30px;
	}

	.projectImg {
		width: 80%;
	}

	.projectName {
		font-size: 0.9em;
	}

	.projectDes {
		font-size: 1.3em;
	}
}

@media only screen and (max-width: 810px) {
	.portfolio {
		width: 240px;
		height: 360px;
		margin-right: 25px;
		float: left;
		padding: 30px;
	}

	.projectImg {
		width: 80%;
	}

	.projectName {
		font-size: 0.8em;
	}

	.projectDes {
		font-size: 1.3em;
	}
}

@media only screen and (max-width: 721px) {
	.portfolio {
		width: 240px;
		height: 381px;
		margin: auto;
		margin-bottom: 50px;
		float: none;
		padding: 30px;
	}

	.projectImg {
		width: 100%;
	}

	.projectName {
		font-size: 0.7em;
	}

	.projectDes {
		font-size: 1.2em;
	}

	.title {
		text-align: center;
	}
}



