.logo {
    width: 120px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: left;
        justify-content: left;
}

.slogan {
	font-size: 80px;
	font-weight: 800;
	color: white;
    text-align: left;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;

}

.slogan:hover{
    scale: 1.05;
}

.description {
	color: white;
    text-align: left;
    font-size: 20px;
}

.possible {
	font-size: 80px;
	font-weight: 800;
    color: #f7a017;
    -webkit-animation-name: changeColor;
            animation-name: changeColor;
    -webkit-animation-duration: 2s;
            animation-duration: 2s;
    -webkit-animation-iteration-count: infinite;
            animation-iteration-count: infinite;
    -webkit-animation-direction: alternate-reverse;
            animation-direction: alternate-reverse;
    -webkit-transition: font-size 1.5s;
    -o-transition: font-size 1.5s;
    transition: font-size 1.5s;
    -webkit-transition: color 1s;
    -o-transition: color 1s;
    transition: color 1s;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;

}

.possible:hover{
    color:#1ac18f;
    -webkit-animation-duration: 0s;
            animation-duration: 0s;
    -webkit-animation-iteration-count: 0;
            animation-iteration-count: 0;
   

}

.holder {
	margin: auto;
	max-width: 1140px;
	overflow: auto;
	padding: 5%;
}

.imgContainer {
	float: left;
}

.peopleImg {
	width: 100%;
    margin-left: 50px;
    -webkit-transition: all 0.5s;
    -o-transition: all 0.5s;
    transition: all 0.5s;
}

.peopleImg:hover{
    scale: 1.05;
}

.titleContainer {
	width: 50%;
	float: left;
}





@media only screen and (max-width: 1231px) {
	.slogan {
		font-size: 55px;
    }
    .possible{
        font-size: 55px;
    }
    
    .peopleImg{
        margin-left: 0px ;
        width: 400px;
    }
}




@media only screen and (max-width: 890px) {
	.slogan {
        font-size: 50px;
        text-align: center;
        
    }
    .possible{
        font-size: 50px;
        text-align: center;
    }
    .description{
        font-size: 15px;
        text-align: center;
    }
    .peopleImg{
        margin-left: 0px ;
        width: 400px;
    }
    .logo {
        width: 100px;
        margin: auto;
    }
    .titleContainer {
        width: 100%;
        margin: auto;
    }

    .imgContainer {
        width: 100%;
        margin: auto;
        margin-top: 30px;;
    }

    .logoContainer{
        width: 100%;
        margin: auto;
        
        
    }
    
    
}

@media only screen and (max-width: 384px) {
	.slogan {
        font-size: 40px;
        text-align: center;
        
    }
    .possible{
        font-size: 40px;
        text-align: center;
    }
    .description{
        font-size: 12px;
        text-align: center;
    }
    .peopleImg{
        margin-left: 0px ;
        width: 250px;
    }
    .logo {
        width: 90px;
        margin: auto;
    }
    .titleContainer {
        width: 100%;
        margin: auto;
    }

    .imgContainer {
        width: 100%;
        margin: auto;
        margin-top: 30px;;
    }

    .logoContainer{
        width: 100%;
        margin: auto;
        
        
    }
    
    
}

@-webkit-keyframes changeColor {
    from {color: #ff5052;}
    to {color:    #f7a017;}
  }

@keyframes changeColor {
    from {color: #ff5052;}
    to {color:    #f7a017;}
  }




