.whatWeDoContainer {
	max-width: 1140px;
	margin: auto;
	padding: 5%;
	overflow: auto;
}

.title {
	text-align: left;
	font-size: 3em;
	font-weight: 800;
	-webkit-transition: font-size 1s;
	-o-transition: font-size 1s;
	transition: font-size 1s;
	-webkit-transition-timing-function: ease-in-out;
	     -o-transition-timing-function: ease-in-out;
	        transition-timing-function: ease-in-out;
}



.categoryContainer {
	float: left;
	overflow: auto;
	width: 260px;
	margin-right: 10.4%;
	-webkit-transition: margin 1s;
	-o-transition: margin 1s;
	transition: margin 1s;
	margin-bottom: 20px;
}

.categoryTitle {
	text-align: left;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
}

.categoryTitle:hover{
	color: #df9723;
}

.categoryLogo {
	width: 48px;
	margin-right: 15px;
	-webkit-transition: all 0.25s;
	-o-transition: all 0.25s;
	transition: all 0.25s;
}

.categoryLogo:hover{
	scale: 0.85;
}
.categoryUL {
	margin-top: 20px;
	color: #5b6e80;
	text-transform: capitalize;
	font-weight: 600;
	list-style: none;
	margin-left: 63px;
	text-align: left;
	padding: 0;
}

.categoryLi {
	margin-top: 20px;
	-webkit-transition: all 0.25s;
	-o-transition: all 0.25s;
	transition: all 0.25s;
}

.categoryLi:hover{
	color: #1a926e;
	margin-left: 20px;
	cursor: default;
}

.categoriesContainer {
	overflow: auto;
	width: 100%;
	margin: auto;
	
}


@media only screen and (max-width: 1270px) {
	.categoryContainer{
		margin-right: 10.3%;
	}
}

@media only screen and (max-width: 1252px) {
	.categoryContainer{
		margin-right: 9.9%;
	}
}

@media only screen and (max-width: 1233px) {
	.categoryContainer{
		margin-right: 7.5%;
	}
}
@media only screen and (max-width: 1120px) {
	.categoryContainer{
		margin-right: 5.5%;
	}
}

@media only screen and (max-width: 1040px) {
	.categoryContainer{
		margin-right: 4%;
	}
}


@media only screen and (max-width: 985px) {
	.categoryContainer{
		margin-right: 7%;
		margin-left: 7%;
	}
	.title {
		text-align: center;
		font-size: 3em;
		font-weight: 800;
	}
}

@media only screen and (max-width: 800px) {
	.categoryContainer{
		margin-right: 6%;
		margin-left: 6%;

	}
}

@media only screen and (max-width: 760px) {
	.categoryContainer{
		margin-right: 5%;
		margin-left: 5%;

	}
}

@media only screen and (max-width: 720px) {
	.categoryContainer{
		margin-right: 4%;
		margin-left: 4%;

	}
}

@media only screen and (max-width: 687px) {
	.categoryContainer{
		margin-right: 3%;
		margin-left: 3%;

	}
}


@media only screen and (max-width: 670px) {
	.categoryContainer{
		margin: auto;
		margin-top: 10px;
	}
	.categoryContainer {
		float: none;	
	}
	.title {	
		font-size: 40px;
		font-weight: 800;
	}
}

@media only screen and (max-width: 290px) {
	
	.title {	
		font-size: 30px;
		
	}

	.categoryTitle{
		font-size: 15px;
	}
}


