.outerFooterContainer{
	max-width: 1140px;
	margin: auto;
	overflow: auto;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}
.innerFooterContainer {
	padding-left: 4%;
	padding-right: 4%;
	margin: auto;
	overflow: auto;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
	
	
}

.footerSlogan {
	font-size: 30px;
	font-weight: 800;
	color: #01070e;
	text-align: left;
	margin: 0px;
	padding-left: 2%;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.footerSlogan2 {
	font-size: 14px;
	line-height: 24px;
	font-weight: 300;
	margin: 0px;
	margin-top: 7px;
	color: #5b6e80;
	text-align: left;
	padding-left: 2%;
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}
.button {
	padding: 16px 32px 12px;
	background-color: #f7a017;
	color: #fff;
	border-radius: 30px;
	-webkit-transition: all .2s ease-in-out;
	-o-transition: all .2s ease-in-out;
	transition: all .2s ease-in-out;
	border-style: none;
	-webkit-transition: all 0.7	s;
	-o-transition: all 0.7	s;
	transition: all 0.7	s;
	font-weight: 800;
}

.button:hover{
	background-color: #16c18e;
	scale: 1.1;
}

.fistbumpGIF {
	width: 64px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.footerContent {
	color: #5b6e80;
	margin-top: 200px;
	
	display: block;
	text-align: center;
	
	margin-bottom: 30px;
	-webkit-transition: all 0.5s;
	-o-transition: all 0.5s;
	transition: all 0.5s;
}

.div1,
.div2,
.div3 {
	width: -webkit-max-content;
	width: -moz-max-content;
	width: max-content;
	float: left;
	margin-top: 70px;
	
}

.div2 {
	margin-left: 2%;
	
}

.div3 {
	float: right;
	margin-right: 10%;
	
}

.div{
	padding-left: 10%;
}


@media only screen and (max-width: 821px) {
	.div1,
	.div2,
	.div3 {
		width: 100%;
		float: none;
		margin-top: 70px;
	}

	.footerSlogan,
	.footerSlogan2{
		width: 100%;
		text-align: center;
	}

	.fistbumpGIF{
		width: 200px;
	}

	.div2,
	.div3{
		margin: 0px;
		margin: auto;
	}	

	.footerContent{
		margin-top: 100px;
	}
}



